.cyberpunk-button {
        background-color: blue;
        color: black;
        font-size: 1rem;
        border: none;
        border-radius: 5px;
        padding: 15px 25px;
        cursor: pointer;
        transition: all 0.3s ease-in-out;
      }
      
      .cyberpunk-checkbox {
        appearance: none;
        width: 18px;
        height: 18px;
        border: 2px solid black;
        border-radius: 5px;
        background-color: transparent;
        display: inline-block;
        position: relative;
        margin-right: 2px;
        cursor: pointer;
      }
      
      .cyberpunk-checkbox:before {
        content: "";
        background-color: blue;
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%) scale(0);
        width: 8px;
        height: 8px;
        border-radius: 3px;
        transition: all 0.3s ease-in-out;
      }
      
      .cyberpunk-checkbox:checked:before {
        transform: translate(-50%, -50%) scale(1);
      }
      
      .cyberpunk-checkbox-label {
        font-size: 1rem;
        color: black;
        cursor: pointer;
        user-select: none;
        display: flex;
        align-items: center;
        margin: 0 2vw;
      }